.hall-of-fame {
    padding: 20px;
}

button {
    margin: 5px;
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
}

select, input, .country-dropdown {
    margin: 5px;
    padding: 5px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.input-section {
    margin-bottom: 20px;
}

.hierarchy {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.role-section {
    margin: 20px 0;
    text-align: center;
    position: relative;
}

.role-heading {
    font-size: 24px;
    font-weight: bold;
    text-decoration: underline;
    position: relative;
    margin-bottom: 10px;
}

.role-heading::after {
    content: '';
    display: block;
    width: 2px;
    height: 20px;
    position: absolute;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%);
}

.members {
    display: flex;
    justify-content: center;
    position: relative;
    margin-top: 20px;
}

.member {
    margin: 0 10px;
    padding: 10px;
    border: 1px solid;
    border-radius: 4px;
    background-color: #c6cccc;
    position: relative;
}

.member::before {
    content: '';
    display: block;
    width: 2px;
    height: 20px;
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
}

.member-since {
    position: absolute;
    top: -10px;
    left: 10px;
    font-size: 12px;
    background-color: #fff;
    padding: 2px 4px;
    border-radius: 4px;
}

.member-name {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
}

.member-flag {
    width: 24px;
    height: 16px;
    margin-left: 8px;
}

.member-year {
    display: block;
    font-size: 14px;
    color: #555;
    margin-top: 5px;
}

.controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.toggle-visibility {
    cursor: pointer;
    font-size: 24px;
}

.red {
    color: red;
}
.green {
    color: green;
}
.yellow {
    color: yellow;
}
.blue {
    color: blue;
}
.cyan {
    color: cyan;
}
.magenta {
    color: magenta;
}
.white {
    color: white;
}
.black {
    color: black;
}

input[type="color"] {
    width: 50px;
    height: 30px;
    border: none;
    cursor: pointer;
    margin-left: 5px;
}
